import Authorization from "./user/auth.service";

const HotelService = {

    async getSearchInfo() {
        try {
            const hotels = await this.getHotels(),
                destinations = await this.getDestinations()
                return {
                    hotels: (hotels) ? hotels : [],
                    destinations: (destinations) ? destinations : [],
                }
        } catch (e) {
            console.log(e)
        }
    }, 
    async getHotels(pole_id=undefined) {
        try {
            const url = (pole_id == undefined) ? `/hotels` : `/hotels/by-pole/${pole_id}`
            const hotels = [],
                res = await Authorization.checkLogin(url)
            if(res){
                if(res.status == 200) {                    
                    for(let h of res.data) {
                        hotels.push({
                            label: h.alias,
                            value: h.id,
                        })
                    }                    
                }
            }
            return hotels
        } catch (e) {
            console.log(e)
        }
    },
    async getDestinations() {
        try {
            const {status, data} = await Authorization.checkLogin(`/poles`)
            
            if(status == 200) {
                const destinations = []
                for(let d of data) {
                    destinations.push({
                        label: d.alias,
                        value: d.id,
                    })
                }
                return destinations
            }
        } catch (e) {
            console.log(e)
        }
    },
    async searchByParam(search_params){
        search_params.auth = 'bearer'
        Authorization.checkLogin('/search/hotels/prices', search_params, 'post').then(e => {

           if(e) {
              this.product_list = e.data
              
              this.product_list.map(p => {                  
                 p.segment = {
                       name: "playa",
                       img: require("@/assets/img/segment/playa.png")
                    }
              })
              // console.log(this.product_list)
           }
        })
    } 
}

export default HotelService