         <Datepicker 
            class="datepicker-container bg-white text-left"
            placeholder="Fecha de entrada y salida"
            v-model="formData.dateRange"
            :format="'dd/MM/yyyy'"
            range multi-calendars 
            auto-apply
            required
            :enable-time-picker="false"
            :close-on-auto-apply="true"
            @update:modelValue="handleDate"
         />
      </div>
      <div class="px-6 pt-4 md:ml-auto">                    
