                  icon: 'warning',
                  confirmButtonColor: '#3085d6',
               })
               return false;           
         }else
         return true;
      },
      formSubmit(e){
         e.preventDefault();
         // Limpiamos el localstorage
         localStorage.removeItem('searchform');
         // guardamos los parametros
         const params = {};
         if(this.formData.category && this.formData.category !== "") params.car_category_id = this.formData.category;
         if(this.formData.engine && this.formData.engine !== "") params.car_engine_id = this.formData.engine;
         params.date_start = this.formData.date[0];
         params.date_out = this.formData.date[1];
         params.type = 'car';
         params.product = 'car';
         // guardamos los parametros en localstorage  
         localStorage.setItem('searchform', JSON.stringify(params))         
         
         if(this.$route.fullPath != '/resultado-de-busqueda'){                
               this.$router.push({name: 'SearchResult'})
         }
         else this.$emit('updparams')

      },
      handleDate(range_date) {
            this.dateRange = {}
            this.dateRange = range_date            
            this.formData.date = [moment(range_date[0]).format('YYYY-MM-DD'), moment(range_date[1]).format('YYYY-MM-DD')]

